import Vue from 'vue';

export default Vue.component('app-comment', {

    template:
        `<div class="app-comment mobile-body-content padding scroll">
            <div id="disqus_thread"></div>
            <div class="dsq-brlink"><a href="mailto:zhangwenli.com"></a> <a href="https://github.com/Ovilia/ovilia.github.io/issues/10">GitHub issue</a> </div>
        </div>`,

    data: function () {
        return {
        };
    },

    mounted: function () {
        var dsq = document.createElement('script');
        dsq.type = 'text/javascript';
        dsq.async = true;
        dsq.src = '//wenli.disqus.com/embed.js';
        (document.getElementsByTagName('head')[0] || document.getElementsByTagName('body')[0]).appendChild(dsq);
    },

    methods: {
    }
});
